				"excerpt": "<p>博客再次迁移，这次是从 Wordpress 转向静态博客（自建）。</p>\n<p>技术栈:</p>\n<ul>\n<li>前端：vue + vue-router + vuex + bootstrap + webpack</li>\n<li>服务端：没有</li>\n<li>数据库：没有</li>\n</ul>\n<p>整站打包后，一次加载所有资源（HTML + CSS + JS + DATA）300K 不到（gzip 后 80K+），秒速渲染，与先前真的是天差地别。</p>\n<p>图片资源从本地服务器搬迁到免费云。 写作使用 Markdown，从此 IDE 写博客不是梦。</p>\n<p>代码地址：</p>\n<p><a href=\"https://github.com/edisond/wxsm-space/\">https://github.com/edisond/wxsm-space/</a></p>\n"
